// imports from plugins
import React, { useState } from "react"
import { Button, Modal } from "antd"

// imports from files of project
import returnAge from "./../return-age/returnAge"
import { BoldSpan } from "./helpers/sales-funnel-styled"

export const SalesFunnelModalStudent = ({ student }) => {

	// data
	const genderFormat = new RegExp(/[Муж|Жен]/g)
	const { name, midname, surname, gender, dateOfBirth, phone, parentPhone, discord, parentFullname, minPaidSubscruption } = student
	const title = `Ученик: ${midname} ${name} ${surname}`
	// data

	// useState
	const [visibleModalOnStudent, setVisibleModalOnStudent] = useState(false)
	// useState

	// Methods
	const showModalOnStudent = () => {
		setVisibleModalOnStudent(true)
	}
	const handleOkOnStudent = () => {
		setVisibleModalOnStudent(false)
	}
	const handleCancelOnStudent = () => {
		setVisibleModalOnStudent(false)
	}
	// Methods

	return (
		<>
			<Button type="primary" size="small" onClick={showModalOnStudent}>Полностью</Button>
			<Modal width={650} title={title} visible={visibleModalOnStudent} onCancel={handleCancelOnStudent}
						 onOk={handleOkOnStudent}>
				<p><BoldSpan>ФИО:</BoldSpan> {name} {midname} {surname}</p>
				<p><BoldSpan>ФИО Родителя:</BoldSpan> {parentFullname}</p>
				<p><BoldSpan>Пол:</BoldSpan> {gender.match(genderFormat).join("")}</p>
				<p><BoldSpan>Возраст:</BoldSpan> {returnAge(dateOfBirth)}</p>
				<p><BoldSpan>Номер родителя:</BoldSpan> {parentPhone}</p>
				<p><BoldSpan>Номер ребёнка:</BoldSpan> {phone || "Не указано"}</p>
				<p><BoldSpan>Ник в дискорде:</BoldSpan> {discord || "Не указано"}</p>
				<p><BoldSpan>Минимальный оплаченный абонемент:</BoldSpan> {minPaidSubscruption || 0}₽</p>
			</Modal>
		</>
	)
}