import React, {useState, useEffect} from 'react'
import {Table, Tag} from 'antd'

import Url from '../../../url/url.js'
import ReturnAge from './../return-age/returnAge.js' // Определяет возраст
import Pagination from './../#more-functions/pagination/pagination.js' // Pagination
import {SalesFunnelModal} from './../sales-funnel/sales-funnel-modal'
import getLimit from './../#more-functions/getLimit/getLimit.js'

// Библиотеки
import './student.css'

import {Link} from "react-router-dom"

import styled from '@emotion/styled'
import errorHandler from "../../error-handler/error-handler";
import {axiosGetFunnelSteps} from "./../sales-funnel/helpers/axios-requests";

const mq = [1020, 700].map(
    bp => `@media (max-width: ${bp}px)`
)

const TableL = styled.div({
    padding: '0 30px',
    [mq[1]]: {
        padding: '0 0',
    },
    '.log_h2': {},
    td: {
        paddingTop: '1px',
        paddingBottom: '4px'
    },
    'th, td': {
        whiteSpace: 'nowrap'
    }
})

const axios = require('axios') // AJAX

let getFunnel = async (fnc) => {
    let box = await axiosGetFunnelSteps(Url, 8)
    fnc(box)
}

const Student = () => {

    const [offsetG, setOffsetG] = useState(0)
    const [Count, setCount] = useState(0)
    const [funnel, setFunnel] = useState([{_id: ''}])

    const [users, setUsers] = useState([
        {
            _id: '45343235456645',
            surname: "Список пуст...",
            name: "",
            midname: "",
            dateOfBirth: "2004-12-07",
            gender: "",
            phoneNumber: "",
            parentPhoneNumber: "",
            discordNickname: "",
            groups: [],
            parentFullname: ''
        }
    ])

    const update = (offset = 0, param = {}, filtersGroups = {}) => {

        let filtersLocal = {}

        for (let key in filtersGroups) {

            if (filtersLocal.groups) {
                filtersLocal.groups.push(key)
            } else {
                filtersLocal.groups = []
                filtersLocal.groups.push(key)
            }
            // filtersLocal.groups = [...filtersLocal.groups]
        }

        for (let key in param) {
            if (param[key][0]) {
                if (key == 'surname' || key == 'name' || key == 'midname' || key == 'dateOfBirth') {
                    filtersLocal[`${key}s`] = param[key]
                } else {
                    filtersLocal[`${key}`] = param[key]
                }
            }
        }

        if (filtersLocal.ages) {
            let dateOfBirth = [...filtersLocal.ages]
            filtersLocal.ages = []
            dateOfBirth.forEach(dateOfBirth => {
                filtersLocal.ages.push(+dateOfBirth)
            })
        }

        if (filtersLocal.balance) {
            let balance = [...filtersLocal.balance]
            if (balance[0] == 1 & !balance[1]) {
                filtersLocal.balance = {$gte: 0}
            } else if (balance[0] == 2 & !balance[1]) {
                filtersLocal.balance = {$lt: 0}
            } else if (balance[1]) {
                filtersLocal.balance = {$gte: 0, $lte: 0}
            }
        }

        let p = {
            filters: filtersLocal
        }

        axios({
            method: 'post',
            url: `${Url}/CRM/Pupils/find?limit=${getLimit}&offset=${offset}`, // ${10 + offset}
            headers: {
                'Content-Type': 'application/jsoncharset=utf-8',
                'Authorization': `Bearer ${localStorage.getItem('tokenID')}`
            },
            data: p
        })
        .then((res) => {

            let {data, headers} = res
            setUsers(data)
            setCount(headers.count)
        })
        .catch((error) => {
            errorHandler(update, error)
        })
    }

    useEffect(() => {
        update()
        getFunnel(setFunnel)
    }, [])

    useEffect(() => {
        document.querySelectorAll("input").forEach((item) => {
            item.value = ''
        })
        document.querySelectorAll('select').forEach((item) => {
            item.value = ''
        })
    })

    const dataSource = []

    users.forEach((item) => {

        const trashUser = async () => {

            axios({
                method: 'DELETE',
                url: `${Url}/CRM/Pupils/${item._id}`,
                headers: {
                    'Content-Type': 'application/jsoncharset=utf-8',
                    'Authorization': `Bearer ${localStorage.getItem('tokenID')}`
                }
            })
            .then((res) => {
                update(offsetG)
            })
            .catch((error) => {
                errorHandler(trashUser, error)
            })
        }

        let {_id, parentFullname, surname, name, midname, dateOfBirth, gender, phone, balance, parentPhone, discord, groups} = item

        dataSource.push({
            key: _id,
            trash: trashUser,
            surname: () => (
                <Link
                    className="link-info"
                    to={`/student/${_id}`}>
                    {surname}
                </Link>
            ),
            name,
            midname,
            dateOfBirth: ReturnAge(dateOfBirth),
            gender,
            phoneNumber: phone,
            tutor: '',
            parentPhoneNumber: parentPhone,
            parentFullname,
            balance: `${balance}`,
            groups: groups ? groups : [],
            discordNickname: discord
        })
    })

    const getColumn = (title, dataIndex) => ({
        title: title,
        dataIndex: dataIndex,
        key: dataIndex
    })

    const columns = [
        {
            title: "",
            dataIndex: "trash",
            key: "trash",
            render: (trash) => {
                if (trash) {
                    return (
                        <i
                            onClick={trash}
                            className="bi bi-trash"
                            style={{
                                fontSize: "30px",
                                color: "#F56767",
                                cursor: "pointer",
                            }}
                        />
                    );
                }
            },
        },
        {
            ...getColumn("Фамилия", "surname"),
            render: (surname) => {
                if (typeof surname == "function") {
                    return surname();
                }
            }
        },
        {
            ...getColumn("Имя", "name")
        },
        {
            ...getColumn("Отчество", "midname")
        },
        {
            ...getColumn("Возраст", "dateOfBirth")
        },
        {
            ...getColumn("Пол", "gender")
        },
        {
            ...getColumn('ФИО Родителя', 'parentFullname')
        },
        {
            ...getColumn(
                "Номер телефона",
                "phoneNumber"
            )
        },
        {
            ...getColumn("Номер телефона родителя", "parentPhoneNumber")
        },
        {
            ...getColumn("Баланс", "balance")
        },
        {
            ...getColumn("Группы", "groups"),
            render: (groups) => {
                if (typeof groups.map == "function") {
                    return (
                        <>
                            {groups.map((item) => (
                                <Tag
                                    color="blue"
                                    key={`${item._id}${item.GROUP_NAME}`}
                                >
                                    {item.GROUP_NAME}
                                </Tag>
                            ))}
                        </>
                    );
                }
            }
        },
        {
            ...getColumn("Дискорд никнейм", "discordNickname")
        },
    ]

    return (
        <>
            <nav id="navbar-student" className="navbar navbar-expand-lg navbar-light">
                <div className="container-fluid d-flex">
                    <div className="collapse d-flex flex-row h3-person">
                        <div className="d-flex plus-box">
                            <div>
                                <h3>Ученики</h3>
                            </div>
                        </div>
                    </div>
                </div>
            </nav>

            <TableL className=''>
                <div>
                    <div className={`table_user`}>
                        <Table
                            pagination={false}
                            dataSource={dataSource}
                            columns={columns}
                            scroll={{x: 0}}>
                        </Table>
                    </div>
                    <Pagination getItem={update} count={Count} offset={offsetG} setOffset={setOffsetG}/>
                </div>
            </TableL>
            <SalesFunnelModal
                loader={{
                    loaded: true, setLoaded: () => {}
                }}
                status={1}
                funnel={funnel}
                pageSize={8}
                Url={Url}
                pupils={{
                    pupilsList: [], setPupilsList: () => {}
                }}
                bigIcon={true}
                update={update}
            />
        </>
    )
}

export default Student