// imports from plugins
import React, {useEffect, useCallback, useState} from "react"
import {Draggable} from "react-beautiful-dnd"
import {Pagination} from "antd"

// imports from files of project
import {FunnelStepStudent} from "./helpers/sales-funnel-styled"
import {Loader} from "./helpers/loader"
import {tooltipCheckerOfTrioString} from "./helpers/tooltip-functions"
import {SalesFunnelModalStudent} from "./sales-funnel-modal-student"
import {axiosGetStudentsEveryStepByPage} from "./helpers/axios-requests"

export const SalesFunnelStudents = ({Url, card, pupils, pageSize}) => {

    // data
    const {pupilsList, setPupilsList} = pupils
    // data

    // useState
    const [loaded, setLoaded] = useState(false)
    const [currentPage, setCurrentPage] = useState(1)
    const [pupilsCount, setPupilsCount] = useState(card.pupilsCount)
    // useState

    // useCallback
    const nextPage = useCallback(async () => {
        setLoaded(prev => prev = true)
        const {students, count} = await axiosGetStudentsEveryStepByPage(Url, {pageSize, currentPage}, card._id)
        setPupilsList(prev => returnNewPupilsList(prev, students, card._id))
        setPupilsCount(prev => prev = count)
        setLoaded(prev => prev = false)
    }, [Url, currentPage, card._id, pageSize, setPupilsList])
    // useCallback

    // useEffect
    useEffect(() => {
        nextPage()
    }, [Url, currentPage, card._id, pageSize, setPupilsList, nextPage])
    // useEffect

    // Methods
    const returnNewPupilsList = (previous, students, cardId) => {
        let newArray

        const olderArray = previous.filter(stud => stud.salesFunnelStep !== cardId)
        let filteredArray = previous.filter(stud => stud.salesFunnelStep === cardId)
        filteredArray = students
        newArray = [...olderArray, ...filteredArray]

        return newArray
    }
    const onChangePaginationHandler = (nextNumberPage) => {
        setCurrentPage(prev => prev = nextNumberPage)
    }
    // Methods

    return (
        <>
            <Pagination simple current={currentPage} total={(Math.ceil(pupilsCount / pageSize) * 10) || 10}
                        onChange={onChangePaginationHandler}/>
            {loaded ? <Loader
                precentage={true}/> : pupilsList.filter(stud => stud.salesFunnelStep === card._id).map((stud, index) => {
                if (card._id === stud.salesFunnelStep) return (
                    <Draggable key={stud._id} draggableId={`${stud._id}`} index={index}>
                        {provided => (
                            <FunnelStepStudent background={card.background}
                                               color={card.color} {...provided.draggableProps} {...provided.dragHandleProps}
                                               ref={provided.innerRef}>
                                <div className="funnelStepStudent__first--info">
                                    <div>
                                        {tooltipCheckerOfTrioString(`${stud.midname} ${stud.name} ${stud.surname}`.length, stud, "", 25, "230px", "ФИО:")}
                                    </div>
                                    <div>
                                        Тел.Родителя: {stud.parentPhone}
                                    </div>
                                    <div>
                                        Мин.Абонемент: {stud.minPaidSubscruption || 0}₽
                                    </div>
                                </div>

                                <SalesFunnelModalStudent student={stud}/>
                            </FunnelStepStudent>
                        )}
                    </Draggable>
                )
            })}
        </>

    )
}