// Student
export const axiosCreateNewStudent = (url, params) => {
  axios.post(`${url}/CRM/Pupils`, {
    name: params.name,
    midname: params.midname,
    surname: params.surname,
    gender: params.gender,
    dateOfBirth: params.dateOfBirth,
    phone: params.phone,
    discord: params.discord,
    parentPhone: params.parentPhone,
    parentFullname: params.parentFullname,
    salesFunnelStep: params.salesFunnelStep
  }, {
    headers: {
      "Authorization": `Bearer ${status ? localStorage.getItem("tokenID") : token}`,
      "Content-Type": "application/json;charset=utf-8"
    }
  })
}
export const axiosGetStudentsEveryStepByPage = async (url, pageInfo, salesFunnelStep) => {
  const {pageSize, currentPage} = pageInfo
  let students

  await axios.get(`${url}/CRM/SalesFunnel/${salesFunnelStep}?limit=${pageSize}&offset=${(currentPage - 1) * pageSize}`, {
    headers: {
      "Authorization": `Bearer ${status ? localStorage.getItem("tokenID") : token}`,
      "Content-Type": "application/json;charset=utf-8"
    }
  }).then(res => students = { students: res.data[0].pupils, count: res.data[0].pupilsCount })

  return students
}
export const axiosUpdateStudent = async (url, id, student) => {
  await axios.patch(`${url}/CRM/Pupils/${id}`, student, {
    headers: {
      "Authorization": `Bearer ${status ? localStorage.getItem("tokenID") : token}`,
      "Content-Type": "application/json;charset=utf-8"
    }
  })
}
// Student

// Funnel
export const axiosGetFunnelSteps = async (url, pageSize) => {
  let salesFunnelSteps

  await axios.get(`${url}/CRM/SalesFunnel?limit=${pageSize}`, {
    headers: {
      "Authorization": `Bearer ${status ? localStorage.getItem("tokenID") : token}`,
      "Content-Type": "application/json;charset=utf-8"
    }
  }).then(res => salesFunnelSteps = res.data)

  return salesFunnelSteps
}
// Funnel
